"use client";

import { useEffect, useState } from "react";
import DataTable, { TableColumn } from "react-data-table-component";

// ==========================
// TypeScript Device Type
// ==========================
type Device = {
  id: number;
  tid: string;
  mid: string;
  merchant_name: string;
  pos_serial: string;
  model: string;
  city: string;
  location: string;
  merchant_address: string;
  telephone: string;
  created_at: string;
};

// ==========================
// Component
// ==========================
const IslamibankPage = () => {
  const [devices, setDevices] = useState<Device[]>([]);
  const [filteredDevices, setFilteredDevices] = useState<Device[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>("");

  // Filters
  const [tidFilter, setTidFilter] = useState<string>("");
  const [cityFilter, setCityFilter] = useState<string>("");
  const [locationFilter, setLocationFilter] = useState<string>("");
  const [posSerialFilter, setPosSerialFilter] = useState<string>("");
  const [modelFilter, setModelFilter] = useState<string>("");

  // Modals
  const [showAddModal, setShowAddModal] = useState(false);
  const [showUploadModal, setShowUploadModal] = useState(false);

  // Form Data
  const [formData, setFormData] = useState({
    tid: "",
    mid: "",
    merchant_name: "",
    pos_serial: "",
    model: "",
    city: "",
    location: "",
    merchant_address: "",
    telephone: "",
  });

  // Editing Device
  const [editingDevice, setEditingDevice] = useState<Device | null>(null);

  // Excel
  const [excelFile, setExcelFile] = useState<File | null>(null);
  const [uploadMsg, setUploadMsg] = useState<string>("");

  // ==========================
  // Fetch Devices
  // ==========================
  useEffect(() => {
    const fetchDevices = async () => {
      setLoading(true);
      setError("");
      const token = localStorage.getItem("access_token"); // ✅ Get token
      if (!token) {
        setError("Unauthorized: No access token found. Please login.");
        setLoading(false);
        return;
      }

      try {
        const response = await fetch("http://127.0.0.1:8000/islami/all", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (response.status === 401) {
          setError("Unauthorized: Invalid or expired token.");
          setLoading(false);
          return;
        }

        if (!response.ok) {
          const err = await response.json();
          throw new Error(err.detail || `HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        setDevices(data.data);
        setFilteredDevices(data.data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchDevices();
  }, []);

  // ==========================
  // Filtering Logic
  // ==========================
  useEffect(() => {
    const filtered = devices.filter((dev) => {
      const matchTid = dev.tid.toLowerCase().includes(tidFilter.toLowerCase());
      const matchCity = dev.city.toLowerCase().includes(cityFilter.toLowerCase());
      const matchLocation = dev.location.toLowerCase().includes(locationFilter.toLowerCase());
      const matchPosSerial = dev.pos_serial.toLowerCase().includes(posSerialFilter.toLowerCase());
      const matchModel = dev.model.toLowerCase().includes(modelFilter.toLowerCase());
      return matchTid && matchCity && matchLocation && matchPosSerial && matchModel;
    });
    setFilteredDevices(filtered);
  }, [tidFilter, cityFilter, locationFilter, posSerialFilter, modelFilter, devices]);

  // ==========================
  // Add / Edit / Delete / Upload
  // ==========================
  const getAuthHeaders = () => {
    const token = localStorage.getItem("access_token");
    return token ? { Authorization: `Bearer ${token}`, "Content-Type": "application/json" } : {};
  };

  const handleAddDevice = async () => {
    const emptyField = Object.entries(formData).find(([_, value]) => value.trim() === "");
    if (emptyField) {
      alert(`❌ Please fill the "${emptyField[0].replace("_", " ")}" field before saving.`);
      return;
    }

    try {
      const response = await fetch("http://127.0.0.1:8000/islami/device", {
        method: "POST",
        headers: getAuthHeaders(),
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        const err = await response.json();
        throw new Error(err.detail || "Failed to add Terminal");
      }

      alert("✅ Terminal added successfully!");
      setShowAddModal(false);
      setFormData({
        tid: "",
        mid: "",
        merchant_name: "",
        pos_serial: "",
        model: "",
        city: "",
        location: "",
        merchant_address: "",
        telephone: "",
      });

      const updated = await fetch("http://127.0.0.1:8000/islami/all", { headers: getAuthHeaders() });
      const newData = await updated.json();
      setDevices(newData.data);
      setFilteredDevices(newData.data);
    } catch (e: any) {
      alert("❌ " + e.message);
    }
  };

  const handleEditClick = (device: Device) => {
    setEditingDevice(device);
    setShowAddModal(true);
    setFormData({ ...device });
  };

  const handleUpdateDevice = async () => {
    if (!editingDevice) return;

    const emptyField = Object.entries(formData).find(([_, value]) => value.trim() === "");
    if (emptyField) {
      alert(`❌ Please fill the "${emptyField[0].replace("_", " ")}" field before saving.`);
      return;
    }

    try {
      const response = await fetch(`http://127.0.0.1:8000/islami/device/${editingDevice.id}`, {
        method: "PUT",
        headers: getAuthHeaders(),
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        const err = await response.json();
        throw new Error(err.detail || "Failed to update Terminal");
      }

      alert("✅ Terminal updated successfully!");
      setShowAddModal(false);
      setEditingDevice(null);

      const updated = await fetch("http://127.0.0.1:8000/islami/all", { headers: getAuthHeaders() });
      const newData = await updated.json();
      setDevices(newData.data);
      setFilteredDevices(newData.data);
    } catch (e: any) {
      alert("❌ " + e.message);
    }
  };

  const handleDelete = async (id: number, pos_serial: string) => {
    const confirm = window.confirm(`Are you sure you want to delete device with POS Serial: ${pos_serial}?`);
    if (!confirm) return;

    try {
      const response = await fetch(`http://127.0.0.1:8000/islami/device/${id}`, {
        method: "DELETE",
        headers: getAuthHeaders(),
      });

      if (!response.ok) {
        const err = await response.json();
        throw new Error(err.detail || "Failed to delete Terminal");
      }

      alert("✅ Terminal deleted successfully!");
      setDevices((prev) => prev.filter((dev) => dev.id !== id));
      setFilteredDevices((prev) => prev.filter((dev) => dev.id !== id));
    } catch (e: any) {
      alert("❌ " + e.message);
    }
  };

  const handleUploadExcel = async () => {
    if (!excelFile) {
      alert("Please select an Excel file first!");
      return;
    }

    const token = localStorage.getItem("access_token");
    const form = new FormData();
    form.append("file", excelFile);

    try {
      const response = await fetch("http://127.0.0.1:8000/islami/upload", {
        method: "POST",
        headers: token ? { Authorization: `Bearer ${token}` } : {},
        body: form,
      });

      const result = await response.json();
      if (!response.ok) throw new Error(result.detail || "Upload failed");

      setUploadMsg("✅ Upload complete successfully!");
      setExcelFile(null);

      const updated = await fetch("http://127.0.0.1:8000/islami/all", { headers: getAuthHeaders() });
      const newData = await updated.json();
      setDevices(newData.data);
      setFilteredDevices(newData.data);
    } catch (err: any) {
      setUploadMsg("❌ " + err.message);
    }
  };

  // ==========================
  // Table Columns
  // ==========================
  const columns: TableColumn<Device>[] = [
    { name: "SL", selector: (_row, i) => i + 1, width: "60px" },
    { name: "TID", selector: (row) => row.tid, sortable: true },
    { name: "MID", selector: (row) => row.mid, sortable: true },
    { name: "Merchant Name", selector: (row) => row.merchant_name, sortable: true },
    { name: "POS Serial", selector: (row) => row.pos_serial },
    { name: "Model", selector: (row) => row.model },
    { name: "City", selector: (row) => row.city },
    { name: "Location", selector: (row) => row.location },
    { name: "Address", selector: (row) => row.merchant_address },
    { name: "Telephone", selector: (row) => row.telephone },
    {
      name: "Created Date",
      selector: (row) => new Date(row.created_at).toLocaleDateString("en-CA"),
      sortable: true,
    },
    {
      name: "Created Time",
      selector: (row) => new Date(row.created_at).toLocaleTimeString("en-GB"),
      sortable: true,
    },
    {
      name: "Actions",
      cell: (row) => (
        <div className="flex gap-2">
          <button onClick={() => handleEditClick(row)} className="bg-yellow-400 text-white px-3 py-1 rounded hover:bg-yellow-500">
            Edit
          </button>
          <button onClick={() => handleDelete(row.id, row.pos_serial)} className="bg-red-600 text-white px-3 py-1 rounded hover:bg-red-700">
            Delete
          </button>
        </div>
      ),
    },
  ];

  if (loading) return <p>Loading...</p>;
  if (error) return <p className="text-red-500">{error}</p>;

  return (
    <div className="p-6 relative">
      <h1 className="text-2xl font-bold mb-4">IslamiBank Terminals</h1>

      {/* Filters, Modals, Table... */}
      {/* Keep the rest of your component JSX as-is */}
    </div>
  );
};

export default IslamibankPage;
